<template>
  <div class="presentation">
    <div class="container">
      <div class="presentation-date">{{ pageData.contentInfo.date }}</div>
      <div class="presentation__content">
        <div class="presentation__content-title">
          {{ pageData.contentInfo.title }}
        </div>
        <div class="presentation__content-line"></div>
        <div class="presentation__content-descr">
          {{ pageData.contentInfo.descr }}
        </div>
        <div class="presentation__content-imgContent">
          <div
            class="presentation__content-imgContent_item"
            v-for="(item, index) in pageData.content"
            :key="index"
          >
            <img
              :src="require('../assets/images/content/' + item.img)"
              alt=""
            />
          </div>
        </div>
        <router-link
          :to="{ name: 'home' }"
          @click="scrollToSection('#loveStory')"
          class="presentation__content-btn"
          >← Вернуться назад</router-link
        >
      </div>
    </div>
  </div>
  <KapibaraFooter />
</template>


<script>
import KapibaraNavigation from "@/components/KapibaraNavigation.vue";
import KapibaraFooter from "@/components/KapibaraFooter.vue";
export default {
  components: {
    KapibaraNavigation,
    KapibaraFooter,
  },
  props: {
    item: Object,
  },
  data() {
    return {};
  },
  methods: {
    goToTop() {
      window.scrollTo(0, 0);
    },
    scrollTo(sectionId) {
      if (this.show) {
        this.show = false;
      }
      const section = document.querySelector(sectionId);
      const scrolDistance = section.offsetTop - 0;
      let distance = window.scrollY;
      const scroll = () => {
        if (scrolDistance > distance + 0.5 || scrolDistance < distance - 0.5) {
          distance -= (distance - scrolDistance) / 15;
          window.scrollTo(0, distance);
          this.timer = setTimeout(() => {
            scroll();
          }, 5);
        } else {
          window.scrollTo(0, scrolDistance);
          stop();
        }
      };
      const stop = () => {
        clearTimeout(this.timer);
        window.removeEventListener("wheel", stop);
      };
      stop();
      window.addEventListener("wheel", stop);
      scroll();
    },
    scrollToSection(sectionId) {
      if (this.$route.name != "home") {
        this.$router.push("/").then(() => {
          setTimeout(() => {
            this.scrollTo(sectionId);
          }, 300);
        });
      } else {
        this.scrollTo(sectionId);
      }
    },
  },
  computed: {
    pageData() {
      return this.$store.getters.getPageData(this.$route.params.id);
    },
  },
  mounted() {},
};
</script>

<style lang="scss">
@import "@/assets/scss/grid/grid";
