import { createRouter, createWebHistory, RouterView } from "vue-router";
import Home from "../views/Home.vue";
import Photoshoots from "../views/Photoshoots.vue";
import Page404 from "../views/Page404.vue";

const photoshootList = "photoshoot-1|photoshoot-2|photoshoot-3|photoshoot-4|photoshoot-5|photoshoot-6|photoshoot-7|photoshoot-8|photoshoot-9|photoshoot-10|photoshoot-11|photoshoot-12|photoshoot-13";

const child = [
  {
    path: "",
    name: "home",
    component: Home,
  },
  {
    path: `photoshoots/:id(${photoshootList})`,
    name: "photoshoots",
    component: Photoshoots,
  },
];

const routes = [
  // {
  //   path: "/",
  //   name: "main",
  //   component: RouterView,
  //   children: child,
  // },
  {
    path: "/tamara-kapibara/",
    name: "main",
    component: RouterView,
    children: child,
  },
  {
    path: "/:pathMatch(.*)*",
    name: "error",
    component: Page404,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
