import photoshoot1 from "@/store/modules/photoshoots/photoshoot1";
import photoshoot2 from "@/store/modules/photoshoots/photoshoot2";
import photoshoot3 from "@/store/modules/photoshoots/photoshoot3";
import photoshoot4 from "@/store/modules/photoshoots/photoshoot4";
import photoshoot5 from "@/store/modules/photoshoots/photoshoot5";
import photoshoot6 from "@/store/modules/photoshoots/photoshoot6";
import photoshoot7 from "@/store/modules/photoshoots/photoshoot7";
import photoshoot8 from "@/store/modules/photoshoots/photoshoot8";
import photoshoot9 from "@/store/modules/photoshoots/photoshoot9";
import photoshoot10 from "@/store/modules/photoshoots/photoshoot10";
import photoshoot11 from "@/store/modules/photoshoots/photoshoot11";
import photoshoot12 from "@/store/modules/photoshoots/photoshoot12";
import photoshoot13 from "@/store/modules/photoshoots/photoshoot13";

const state = () => {
  return {
    photoshootData: {
      'photoshoot-1': photoshoot1,
      'photoshoot-2': photoshoot2,
      'photoshoot-3': photoshoot3,
      'photoshoot-4': photoshoot4,
      'photoshoot-5': photoshoot5,
      'photoshoot-6': photoshoot6,
      'photoshoot-7': photoshoot7,
      'photoshoot-8': photoshoot8,
      'photoshoot-9': photoshoot9,
      'photoshoot-10': photoshoot10,
      'photoshoot-11': photoshoot11,
      'photoshoot-12': photoshoot12,
      'photoshoot-13': photoshoot13,
    },
  };
};
const getters = {
  getPageData: (state) => (id) => {
    return state.photoshootData[id];
  },
  getContentInfo: (state) => () => {
    const array = [];

    Object.keys(state.photoshootData).forEach((element) => {
      array.push(state.photoshootData[element].contentInfo);
    });
    return array;
  },
};
export default {
  state,
  getters,
};
