  &__content {
    width: 100%;
    &-form {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      border-bottom: 1px solid #181818;
      @media (max-width: 768px) {
        flex-direction: column;
        align-items: center;
      }
      &-title {
        @include adaptiv-fontlg(36, 25);
