        @media (max-width: 768px) {
          @include adaptiv-fontmd(63, 25);
          @include adaptiv-lineHeightmd(98, 28);
        }
      }
      .form {
        width: 70%;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        @include adaptivMarginlg(0, 0, 0, 20, 0, 0, 0, 10);
        @media (max-width: 768px) {
          width: 100%;
