          @include adaptivHeightlg(50, 30);
          border-radius: 50%;
          border: 1px solid rgb(0 0 0 / 12%);
          display: flex;
          align-items: center;
          justify-content: center;
          transition: 0.5s;
          @media (max-width: 768px) {
            @include adaptivWidthmd(60, 50);
