          }
          &:hover {
            border: 1px solid #181818;
            transition: 0.5s;
          }
        }
      }
      &-descr {
        @include adaptiv-fontlg(16, 12);
        @include adaptiv-lineHeightlg(26, 23);
        font-family: "Helvetica-roman";
        color: #656565;
        @include adaptivMarginlg(0, 0, 30, 0, 0, 0, 20, 0);
        @media (max-width: 768px) {
          width: 85%;
